import React from 'react';
import { BrowserRouter as Router, Route, Switch, Link } from 'react-router-dom';


import 'bootstrap/dist/css/bootstrap.min.css'

import './App.css';

import { NotFound, Home, BlogList, BlogDetails, Login, Register as SignUp } from './page';


function App() {
   return (
      <Router>
         <div className="App">
            <header>
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                  <div className="container">
                      <a className="navbar-brand" href="/">Example Blog</a>
                      <button className="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
                          <span className="navbar-toggler-icon"></span>
                      </button>
                      <div className="collapse navbar-collapse" id="navbarResponsive">
                          <ul className="navbar-nav ml-auto">
                              <li className="nav-item">
                                  <Link className="nav-link" to="/posts">Posts</Link>
                              </li>
                              <li className="nav-item">
                                  <Link className="nav-link" to="/login">Login</Link>
                              </li>
                              <li className="nav-item">
                                  <Link className="nav-link" to="/register">Register</Link>
                              </li>
                          </ul>
                      </div>
                  </div>
              </nav>
            </header>
            <Switch>
               <Route path='react/posts/:id' component={BlogDetails} />
               <Route path='react/login' component={Login} />
               <Route path='react/register' component={SignUp} />
               <Route path='react/posts' exact component={BlogList} />
               <Route path="react/" exact component={Home} />
               <Route exact component={NotFound} />
            </Switch>
            <footer className="py-5 bg-dark">
                <div className="container">
                    <p className="m-0 text-center text-white">Copyright © Gray Space It LTD 2020</p>
                </div>
            </footer>
         </div>
      </Router>
   );
}

export default App;
